import { NextRequest, NextResponse } from 'next/server'
import { createClient } from '@/lib/supabase/server'
import { createAdminClient } from '@/lib/supabase/admin'

export async function GET(request: NextRequest) {
  try {
    const supabase = await createClient()
    const { data: { user } } = await supabase.auth.getUser()

    if (!user) {
      return NextResponse.json({ error: 'Not authenticated' }, { status: 401 })
    }

    const adminSupabase = createAdminClient()

    // Get school admin
    const { data: schoolAdmin } = await adminSupabase
      .from('school_admins')
      .select('school_id')
      .eq('user_id', user.id)
      .single()

    if (!schoolAdmin) {
      return NextResponse.json({ error: 'School admin not found' }, { status: 404 })
    }

    const schoolId = schoolAdmin.school_id

    // Get total students
    const { count: studentsCount } = await adminSupabase
      .from('students')
      .select('*', { count: 'exact', head: true })
      .eq('school_id', schoolId)
      .eq('is_active', true)

    // Get total batches
    const { count: batchesCount } = await adminSupabase
      .from('batches')
      .select('*', { count: 'exact', head: true })
      .eq('school_id', schoolId)
      .eq('is_active', true)

    // Get today's attendance
    const today = new Date().toISOString().split('T')[0]
    const { count: attendanceCount } = await adminSupabase
      .from('attendance')
      .select('*', { count: 'exact', head: true })
      .eq('school_id', schoolId)
      .eq('date', today)
      .eq('status', 'present')

    // Calculate attendance percentage
    const attendancePercentage = studentsCount && attendanceCount 
      ? Math.round((attendanceCount / studentsCount) * 100)
      : 0

    // Get recent announcements
    const { data: announcements } = await adminSupabase
      .from('announcements')
      .select(`
        *,
        batches (name)
      `)
      .eq('school_id', schoolId)
      .eq('is_active', true)
      .order('created_at', { ascending: false })
      .limit(5)

    return NextResponse.json({
      stats: {
        totalStudents: studentsCount || 0,
        totalBatches: batchesCount || 0,
        todayAttendance: attendanceCount || 0,
        attendancePercentage
      },
      announcements: announcements || []
    })
  } catch (error) {
    console.error('Error fetching dashboard stats:', error)
    return NextResponse.json({ error: 'Internal server error' }, { status: 500 })
  }
}
